import React from 'react';

/**
 *      
 *
 * @param {Object} props
 * @param {Array} props.fields -    
 * @param {Object} props.values -    
 * @param {Function} props.onChange -    (key, value) => void
 * @param {Function} props.onSubmit -    
 * @param {string} props.title -    (: '')
 * @param {string} props.submitText -    (: ' ')
 *
 * @example
 * const fields = [
 *   {
 *     key: 'memberName',
 *     label: '',
 *     type: 'text',
 *     gridCol: 'full',
 *     placeholder: ' ',
 *   },
 *   {
 *     key: 'startDate',
 *     label: '',
 *     type: 'date',
 *     gridCol: 'half',
 *   },
 * ];
 *
 * <SearchForm
 *   fields={fields}
 *   values={formValues}
 *   onChange={(key, value) => setFormValues({ ...formValues, [key]: value })}
 *   onSubmit={handleSearch}
 *   title=" "
 * />
 */
const SearchForm = ({ fields = [], values = {}, onChange, onSubmit, title = '', submitText = ' ' }) => {
  //    
  const renderSearchField = field => {
    const commonProps = {
      className: 'form-control',
      value: values[field.key] || '',
      onChange: e => onChange(field.key, e.target.value),
    };

    switch (field.type) {
      case 'date':
        return <input type="date" {...commonProps} />;

      case 'text':
        return <input type="text" placeholder={field.placeholder} {...commonProps} />;

      case 'number':
        return (
          <input
            type="number"
            placeholder={field.placeholder}
            min={field.min}
            max={field.max}
            step={field.step}
            {...commonProps}
          />
        );

      case 'select':
        return (
          <select {...commonProps} className="form-select">
            <option value="">{field.placeholder || ''}</option>
            {field.options?.map(opt => (
              <option key={opt.value} value={opt.value}>
                {opt.label}
              </option>
            ))}
          </select>
        );

      case 'textarea':
        return <textarea placeholder={field.placeholder} rows={field.rows || 3} {...commonProps} />;

      default:
        return null;
    }
  };

  //   
  const renderSearchRows = () => {
    const rows = [];
    let currentRow = [];

    fields.forEach((field, index) => {
      if (field.gridCol === 'half') {
        currentRow.push(field);

        // 2     
        if (currentRow.length === 2 || index === fields.length - 1) {
          rows.push([...currentRow]);
          currentRow = [];
        }
      } else if (field.gridCol === 'full') {
        //   half     
        if (currentRow.length > 0) {
          rows.push([...currentRow]);
          currentRow = [];
        }
        // full   
        rows.push([field]);
      }
    });

    return rows.map((rowFields, rowIndex) => (
      <dl key={rowIndex} className="w-100 row align-items-center mb-2">
        {rowFields.map(field => (
          <React.Fragment key={field.key}>
            <dt className={`col-2 text-end`} style={{ margin: '0px', padding: '0px' }}>
              {field.label} :
            </dt>
            <dd
              className={`col-${field.gridCol === 'full' ? '10' : '4'}`}
              style={{ margin: '0px', padding: '0px 15px' }}
            >
              {renderSearchField(field)}
            </dd>
          </React.Fragment>
        ))}
      </dl>
    ));
  };

  const handleSubmit = e => {
    e.preventDefault();
    onSubmit?.();
  };

  return (
    <div className="card mb-4 shadow-sm" style={{ maxWidth: '900px', margin: '0 auto' }}>
      <div className="card-header bg-light fw-bold"> {title}</div>
      <div className="card-body">
        <form onSubmit={handleSubmit}>
          <div className="w-100 d-flex flex-column align-items-center">
            {renderSearchRows()}

            {/*   */}
            <div className="col-12 text-center mt-3">
              <button type="submit" className="btn btn-primary px-4">
                {submitText}
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default SearchForm;
