import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { removeLike } from '../../../features/user/userSlice';
import api from 'app/api';
import './LikeList.css';

const LikeList = ({ likes = { products: [], brands: [] } }) => {
  const dispatch = useDispatch();
  const { profile } = useSelector(state => state.user);
  const userNo = profile?.userNo;

  const [activeTab, setActiveTab] = useState('products'); // 'products' | 'brands'

  const items = activeTab === 'products' ? likes.products || [] : likes.brands || [];

  const handleAddToCart = async productId => {
    try {
      await api.post('/api/cart/items', {
        productId: productId,
        quantity: 1,
      });
      alert(' .');
    } catch (error) {
      console.error('  :', error);
      alert('  .');
    }
  };

  const handleDelete = async productId => {
    if (window.confirm('  ?')) {
      await dispatch(removeLike({ userNo, productId, type: 'PRODUCT' }));
    }
  };

  return (
    <div className="like-list-container">
      <h2 className="page-title"></h2>

      <div className="tabs">
        <button
          className={`tab-btn ${activeTab === 'products' ? 'active' : ''}`}
          onClick={() => setActiveTab('products')}
        >
          
        </button>
        <button className={`tab-btn ${activeTab === 'brands' ? 'active' : ''}`} onClick={() => setActiveTab('brands')}>
          
        </button>
      </div>

      <div className="like-content">
        <div className="list-info">
          <span>
             <span className="count">{items.length}</span>
          </span>
          <span>
            |    <span className="highlight">120</span> .
          </span>
        </div>{' '}
        <div className="like-table-header">
          {activeTab === 'products' ? (
            <>
              <span className="col-product"></span>
              <span className="col-price"></span>
              <span className="col-manage"></span>
            </>
          ) : (
            <>
              <span className="col-brand"></span>
              <span className="col-desc"></span>
              <span className="col-manage"></span>
            </>
          )}
        </div>
        {items.length === 0 ? (
          <div className="empty-list">
            <div className="empty-icon">!</div>
            <p> {activeTab === 'products' ? '' : ''} .</p>
          </div>
        ) : (
          <div className="like-items">
            {items.map(item => (
              <div key={item.id} className="like-item">
                {activeTab === 'products' ? (
                  <>
                    <div className="col-product">
                      <img
                        src={`${process.env.PUBLIC_URL}/images/product/${item.image}`}
                        alt={item.name}
                        className="item-thumb"
                      />
                      <div className="item-text">
                        <span className="item-brand">{item.brand}</span>
                        <span className="item-name">{item.name}</span>
                        <div className="item-tags">
                          {item.tags &&
                            item.tags.map(tag => (
                              <span key={tag} className="tag">
                                #{tag}
                              </span>
                            ))}
                        </div>
                      </div>
                    </div>
                    <div className="col-price">
                      {item.salePrice ? (
                        <div className="price-box">
                          <span className="original-price">{item.price.toLocaleString()}</span>
                          <span className="sale-price">{item.salePrice.toLocaleString()}</span>
                          <span className="discount-rate">{item.discount}%</span>
                        </div>
                      ) : (
                        <span className="normal-price">{item.price.toLocaleString()}</span>
                      )}
                    </div>
                    <div className="col-manage">
                      <button className="btn-cart" onClick={() => handleAddToCart(item.productId)}>
                        
                      </button>
                      <button className="btn-delete" onClick={() => handleDelete(item.productId)}>
                        
                      </button>
                    </div>
                  </>
                ) : (
                  <>
                    <div className="col-brand">
                      <img
                        src={`${process.env.PUBLIC_URL}/images/product/${item.image}`}
                        alt={item.name}
                        className="brand-thumb"
                      />
                      <span className="brand-name">{item.name}</span>
                    </div>
                    <div className="col-desc">{item.desc}</div>
                    <div className="col-manage">
                      <button className="btn-delete"></button>
                    </div>
                  </>
                )}
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default LikeList;
