import React, { useState, useEffect } from 'react';
import { Modal, Button, Form, Row, Col } from 'react-bootstrap';
import { FEEDBACK_OPTIONS } from '../../common/reviewUtils';
import api from 'lib/apiClient';

const ReviewWriteModal = ({ show, onHide, prdNo, odNo, userInfo, onReviewSubmitted }) => {
  const [rating, setRating] = useState(5);
  const [content, setContent] = useState('');
  const [selectedFeedbacks, setSelectedFeedbacks] = useState([]); //   [101, 501]

  //    state
  const [skinType, setSkinType] = useState(0);
  const [skinTone, setSkinTone] = useState(0);

  //  
  const [previewImages, setPreviewImages] = useState([]); //  URL
  const [uploadFiles, setUploadFiles] = useState([]); //   File 

  //    
  useEffect(() => {
    if (!show) {
      setRating(5);
      setContent('');
      setSelectedFeedbacks([]);
      setPreviewImages([]);
      setUploadFiles([]);

      setSkinType(userInfo?.userSkin || 0);
      setSkinTone(userInfo?.userColor || 0);
    }
  }, [show, userInfo]);

  //   
  const toggleFeedback = code => {
    if (selectedFeedbacks.includes(code)) {
      setSelectedFeedbacks(selectedFeedbacks.filter(c => c !== code));
    } else {
      setSelectedFeedbacks([...selectedFeedbacks, code]);
    }
  };

  //    
  const handleImageChange = e => {
    const files = Array.from(e.target.files);
    if (files.length + uploadFiles.length > 5) {
      alert('  5  .');
      return;
    }

    const newPreviews = files.map(file => URL.createObjectURL(file));
    setPreviewImages([...previewImages, ...newPreviews]);
    setUploadFiles([...uploadFiles, ...files]);
  };

  //   
  const removeImage = index => {
    setPreviewImages(previewImages.filter((_, i) => i !== index));
    setUploadFiles(uploadFiles.filter((_, i) => i !== index));
  };

  //    (Multipart/form-data)
  const handleSubmit = async () => {
    if (content.length < 10) {
      alert('   10  .');
      return;
    }

    try {
      const formData = new FormData();

      // 1. JSON  
      const jsonData = {
        userNo: userInfo?.userNo || 0, //   
        prdNo: prdNo,
        revStar: rating,
        content: content,
        odNo: odNo || 0, //  0,   
        userSkin: Number(skinType), //state 
        userColor: Number(skinTone) === 0 ? null : Number(skinTone), // state 
        feedback: selectedFeedbacks, // [101, 501]  
        revRank: userInfo?.revRank || 1,
      };

      // 2. JSON Blob  'data'   (Spring @RequestPart("data") )
      const jsonBlob = new Blob([JSON.stringify(jsonData)], { type: 'application/json' });
      formData.append('data', jsonBlob);

      // 3.  'files'   (Spring @RequestPart("files") )
      uploadFiles.forEach(file => {
        formData.append('files', file);
      });

      // 4. API 
      const response = await api.post(`/api/products/${prdNo}/reviews`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });

      if (response.data && response.data.resultMsg === 'SUCCESS') {
        alert(' !');
        onHide();
        if (onReviewSubmitted) onReviewSubmitted();
      } else {
        // 
        let errorMsg = response.data.resultMsg || '   ';

        // "BAD_REQUEST: "  
        if (errorMsg.includes(': ')) {
          errorMsg = errorMsg.split(': ')[1];
        }

        console.error('  :', response.data);
        alert(errorMsg); // errorMsg 
        if (errorMsg.includes('') || errorMsg.includes('')) {
          onHide();
        }
      }
    } catch (error) {
      console.error('  ', error);
      alert('    .');
    }
  };

  return (
    <Modal show={show} onHide={onHide} centered size="lg">
      <Modal.Header closeButton>
        <Modal.Title> </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {/*  */}
        <div className="text-center mb-4">
          <div style={{ fontSize: '2rem', cursor: 'pointer', color: '#ffc107' }}>
            {[1, 2, 3, 4, 5].map(star => (
              <span key={star} onClick={() => setRating(star)}>
                {star <= rating ? '' : ''}
              </span>
            ))}
          </div>
          <div className="fw-bold">{rating}</div>
        </div>

        <Row className="mb-4">
          <Col xs={6}>
            <Form.Group>
              <Form.Label className="fw-bold small"> </Form.Label>
              <Form.Select value={skinType} onChange={e => setSkinType(e.target.value)} size="sm">
                <option value="0"></option>
                <option value="1"></option>
                <option value="2"></option>
                <option value="3"></option>
                <option value="4"></option>
                <option value="5"></option>
              </Form.Select>
            </Form.Group>
          </Col>
          <Col xs={6}>
            <Form.Group>
              <Form.Label className="fw-bold small"> </Form.Label>
              <Form.Select value={skinTone} onChange={e => setSkinTone(e.target.value)} size="sm">
                <option value="0"></option>
                <option value="1"></option>
                <option value="2"></option>
                <option value="3"></option>
                <option value="4"></option>
              </Form.Select>
            </Form.Group>
          </Col>
        </Row>

        {/*   */}
        <div className="mb-3">
          <Form.Label className="fw-bold">  ?</Form.Label>
          <div className="d-flex flex-wrap gap-2">
            {FEEDBACK_OPTIONS.map(opt => (
              <Button
                key={opt.code}
                variant={
                  selectedFeedbacks.includes(opt.code)
                    ? opt.type === 'POSITIVE'
                      ? 'success'
                      : 'warning'
                    : 'outline-secondary'
                }
                size="sm"
                onClick={() => toggleFeedback(opt.code)}
                className="rounded-pill"
              >
                {opt.label}
              </Button>
            ))}
          </div>
        </div>

        {/*   */}
        <Form.Group className="mb-3">
          <Form.Control
            as="textarea"
            rows={5}
            placeholder="    . (10 )"
            value={content}
            onChange={e => setContent(e.target.value)}
          />
        </Form.Group>

        {/*   */}
        <Form.Group className="mb-3">
          <Form.Label>  ({uploadFiles.length}/5)</Form.Label>
          <Form.Control type="file" multiple accept="image/*" onChange={handleImageChange} />
          <div className="d-flex gap-2 mt-2 overflow-auto">
            {previewImages.map((src, idx) => (
              <div key={idx} style={{ position: 'relative', width: '80px', height: '80px' }}>
                <img
                  src={src}
                  alt="preview"
                  style={{ width: '100%', height: '100%', objectFit: 'cover', borderRadius: '5px' }}
                />
                <button
                  onClick={() => removeImage(idx)}
                  style={{
                    position: 'absolute',
                    top: -5,
                    right: -5,
                    borderRadius: '50%',
                    border: 'none',
                    background: 'red',
                    color: 'white',
                    width: '20px',
                    height: '20px',
                    fontSize: '12px',
                    lineHeight: '1',
                  }}
                >
                  X
                </button>
              </div>
            ))}
          </div>
        </Form.Group>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={onHide}>
          
        </Button>
        <Button variant="primary" onClick={handleSubmit}>
          
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default ReviewWriteModal;
