import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import Step1SkinType from '../components/user/skinprofile/Step1SkinType';
import '../styles/SkinProfileSetup.css';

const SkinProfileSetupPage = () => {
  const navigate = useNavigate();
  const [profile, setProfile] = useState({ skinType: null });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleSkinTypeSelect = skinType => {
    setProfile({ skinType });
  };

  const handleSave = async () => {
    if (!profile.skinType) {
      setError('  ');
      return;
    }

    setLoading(true);
    setError(null);

    try {
      // : access_token  token
      const token = localStorage.getItem('token');
      if (!token) {
        throw new Error(' ');
      }

      const response = await axios.post(
        `${process.env.REACT_APP_API_URL}/api/user/skin-profile`,
        { skinType: profile.skinType },
        {
          headers: {
            Authorization: `Bearer ${token}`,
            'Content-Type': 'application/json',
          },
        },
      );

      if (response.data.resultCode === 200) {
        // localStorage user  
        const userStr = localStorage.getItem('user');
        if (userStr) {
          try {
            const user = JSON.parse(userStr);
            user.userSkin = profile.skinType;
            localStorage.setItem('user', JSON.stringify(user));
          } catch (e) {
            console.error('user  :', e);
          }
        }

        alert('  !');
        navigate('/');
      } else {
        setError(response.data.resultMsg || ' ');
      }
    } catch (err) {
      setError(err.message || ' ');
      console.error('   :', err);
    } finally {
      setLoading(false);
    }
  };

  const handleSkip = () => {
    navigate('/');
  };

  return (
    <div className="skin-profile-container">
      <div className="skin-profile-wrapper">
        <Step1SkinType selectedValue={profile.skinType} onSelect={handleSkinTypeSelect} />

        {error && <div className="error-message">{error}</div>}

        <div className="button-group">
          <button className="btn-skip" onClick={handleSkip} disabled={loading}>
            
          </button>
          <button className="btn-save" onClick={handleSave} disabled={loading || !profile.skinType}>
            {loading ? ' ...' : ' '}
          </button>
        </div>
      </div>
    </div>
  );
};

export default SkinProfileSetupPage;
