import React, { useState, useEffect } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import axios from 'axios';
import { useSelector } from 'react-redux';

import OrderPrdTable from './OrderPrdTable';
import AddressFields from 'components/common/AddressFields';

const OrderDeliveryAdd = () => {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const apiBaseUrl = useSelector(state => state.admConfig.apiBaseUrl);

  const [item, setItem] = useState({
    odNo: '',
    qnaNo: '',
    delvCompany: '',
    delvComNum: '',
    delvType: 11,
    delvGetName: '',
    delvGetHp: '',
    delvGetZip: '',
    delvGetJibunAddr: '',
    delvGetRoadAddr: '',
    delvGetDetailAddr: '',
    delvGetBcCode: '',
  });

  // Get Param
  useEffect(() => {
    const odNoParam = searchParams.get('od_no');
    const qnaNoParam = searchParams.get('qna_no');
    if (odNoParam) {
      setItem(prev => ({
        ...prev,
        odNo: odNoParam || prev.odNo,
        qnaNo: qnaNoParam || prev.qnaNo,
      }));
    }
  }, [searchParams]);

  const handleChange = e => {
    const { name, value } = e.target;
    setItem(prev => ({ ...prev, [name]: value }));
  };

  const handleSave = async () => {
    try {
      await axios.post(`${apiBaseUrl}/orders/delivery`, item);
      alert('   !');
      navigate('/admin/order/delivery');
    } catch (err) {
      console.error(err);
      alert('    .');
    }
  };

  return (
    <div className="container mt-4" style={{ maxWidth: '900px' }}>
      <h2 className="mb-4 text-center">   </h2>
      <div className="card shadow-sm mb-4">
        <div className="card-body">
          {/*      */}
          {item.odNo && <OrderPrdTable odInfo={{ odNo: item.odNo }} apiBaseUrl={apiBaseUrl} />}

          {/*    */}
          <table className="table table-bordered">
            <colgroup>
              <col style={{ width: '20%' }} />
              <col style={{ width: '30%' }} />
              <col style={{ width: '20%' }} />
              <col style={{ width: '30%' }} />
            </colgroup>
            <tbody>
              <tr>
                <th className="bg-light"> (odNo)</th>
                <td>
                  <input
                    type="number"
                    name="odNo"
                    className="form-control"
                    value={item.odNo}
                    onChange={handleChange}
                    required
                  />
                </td>
                <th className="bg-light"> </th>
                <td>
                  <select name="delvType" className="form-select" value={item.delvType} onChange={handleChange}>
                    <option value={11}></option>
                    <option value={12}></option>
                    <option value={13}></option>
                    <option value={21}></option>
                    <option value={22}></option>
                    <option value={31}></option>
                  </select>
                </td>
              </tr>

              <tr>
                <th className="bg-light"></th>
                <td>
                  <input
                    type="text"
                    name="delvCompany"
                    className="form-control"
                    value={item.delvCompany}
                    onChange={handleChange}
                    required
                  />
                </td>
                <th className="bg-light"> </th>
                <td>
                  <input
                    type="text"
                    name="delvComNum"
                    className="form-control"
                    value={item.delvComNum}
                    onChange={handleChange}
                    required
                  />
                </td>
              </tr>

              <tr>
                <th className="bg-light"> </th>
                <td>
                  <input
                    type="text"
                    name="delvGetName"
                    className="form-control"
                    value={item.delvGetName}
                    onChange={handleChange}
                    required
                  />
                </td>
                <th className="bg-light"> </th>
                <td>
                  <input
                    type="text"
                    name="delvGetHp"
                    className="form-control"
                    value={item.delvGetHp}
                    onChange={handleChange}
                    required
                  />
                </td>
              </tr>
              {/*     */}
              <AddressFields item={item} setItem={setItem} handleChange={handleChange} prefix="delvGet" />
            </tbody>
          </table>

          <div className="text-center mt-4">
            <button className="btn btn-secondary me-2" onClick={() => navigate(-1)}>
               
            </button>
            <button className="btn btn-success" onClick={handleSave}>
               
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default OrderDeliveryAdd;
