import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useSelector } from 'react-redux';

import { LoadingOverlay } from 'components/common/commonUtils';

const ProductAdd = () => {
  const navigate = useNavigate();
  const apiBaseUrl = useSelector(state => state.admConfig.apiBaseUrl);

  const [loading, setLoading] = useState(false);

  // : product + prdDetail
  const [item, setItem] = useState({
    product: {
      prdName: '',
      prdPrice: '',
      prdVolume: '',
      prdCompany: '',
      prdStock: '',
      prdStatus: '',
      prdMainCate: '',
      prdSubCate: '',
      prdImg: '',
      prdDesc: '',
    },
    prdDetail: {
      prdVolume_text: '',
      prdSpec: '',
      prdExpire: '',
      prdUsage: '',
      prdManuf: '',
      prdOrigin: '',
      prdFda: '',
      prdIngredients: '',
      prdCaution: '',
      prdQuality: '',
      prdCs_phone: '',
    },
  });

  //   (product / prdDetail )
  const handleChange = (e, section = 'product') => {
    const { name, value } = e.target;
    setItem(prev => ({
      ...prev,
      [section]: {
        ...prev[section],
        [name]: value,
      },
    }));
  };

  const [previewImg, setPreviewImg] = useState('');
  //     
  const handleImageChange = e => {
    const file = e.target.files[0];
    if (!file) return;

    // DB  
    setItem(prev => ({
      ...prev,
      product: {
        ...prev.product,
        prdImg: file.name,
      },
    }));

    //  
    const reader = new FileReader();
    reader.onload = () => setPreviewImg(reader.result);
    reader.readAsDataURL(file);
  };

  // 
  const handleSave = async () => {
    try {
      setLoading(true);
      await axios.post(`${apiBaseUrl}/products`, item);
      alert('   !');
      navigate('/admin/product/list');
    } catch (err) {
      console.error(err);
      alert('     .');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="container mt-4" style={{ maxWidth: '900px' }}>
      <LoadingOverlay show={loading} />

      <h2 className="mb-4 text-center">   </h2>

      <div className="card shadow-sm mb-4">
        <div className="card-body">
          {/*   */}
          <div className="text-center mb-4">
            <img
              src={previewImg || '/images/default.png'}
              alt=" "
              style={{
                width: '200px',
                height: '200px',
                objectFit: 'cover',
                borderRadius: '12px',
                border: '1px solid #ddd',
              }}
            />
            <div className="mt-2">
              <input type="file" onChange={handleImageChange} />
            </div>
            <div className="mt-1 text-muted">{item.product.prdImg || '  '}</div>
          </div>

          {/*    */}
          <table className="table table-bordered">
            <colgroup>
              <col style={{ width: '20%' }} />
              <col style={{ width: '30%' }} />
              <col style={{ width: '20%' }} />
              <col style={{ width: '30%' }} />
            </colgroup>
            <tbody>
              <tr>
                <th className="bg-light"></th>
                <td>
                  <input
                    type="text"
                    name="prdName"
                    className="form-control"
                    value={item.product.prdName}
                    onChange={e => handleChange(e, 'product')}
                    required
                  />
                </td>
                <th className="bg-light"></th>
                <td>
                  <input
                    type="text"
                    name="prdCompany"
                    className="form-control"
                    value={item.product.prdCompany}
                    onChange={e => handleChange(e, 'product')}
                  />
                </td>
              </tr>

              <tr>
                <th className="bg-light"></th>
                <td>
                  <input
                    type="number"
                    name="prdVolume"
                    className="form-control"
                    value={item.product.prdVolume}
                    onChange={e => handleChange(e, 'product')}
                  />
                </td>
                <th className="bg-light"></th>
                <td>
                  <input
                    type="number"
                    name="prdStock"
                    className="form-control"
                    value={item.product.prdStock}
                    onChange={e => handleChange(e, 'product')}
                  />
                </td>
              </tr>

              <tr>
                <th className="bg-light"></th>
                <td>
                  <input
                    type="number"
                    name="prdPrice"
                    className="form-control"
                    value={item.product.prdPrice}
                    onChange={e => handleChange(e, 'product')}
                    required
                  />
                </td>
                <th className="bg-light"> </th>
                <td>
                  <input
                    type="text"
                    name="prdImg"
                    className="form-control"
                    value={item.product.prdImg}
                    onChange={e => handleChange(e, 'product')}
                    placeholder="  URL"
                  />
                </td>
              </tr>

              <tr>
                <th className="bg-light"> </th>
                <td>
                  <input
                    type="number"
                    name="prdMainCate"
                    className="form-control"
                    value={item.product.prdMainCate}
                    onChange={e => handleChange(e, 'product')}
                  />
                </td>
                <th className="bg-light"> </th>
                <td>
                  <input
                    type="number"
                    name="prdSubCate"
                    className="form-control"
                    value={item.product.prdSubCate}
                    onChange={e => handleChange(e, 'product')}
                  />
                </td>
              </tr>

              <tr>
                <th className="bg-light"> </th>
                <td colSpan="3">
                  <textarea
                    name="prdDesc"
                    className="form-control"
                    value={item.product.prdDesc}
                    onChange={e => handleChange(e, 'product')}
                  />
                </td>
              </tr>
            </tbody>
          </table>

          {/*    */}
          <h5 className="fw-bold border-bottom pb-2 mb-3 text-primary"> </h5>
          <table className="table table-bordered">
            <tbody>
              <tr>
                <th className="bg-light">  </th>
                <td>
                  <input
                    type="text"
                    name="prdVolume_text"
                    className="form-control"
                    value={item.prdDetail.prdVolume_text}
                    onChange={e => handleChange(e, 'prdDetail')}
                  />
                </td>
                <th className="bg-light">
                    
                  <br />
                  (  )
                </th>
                <td>
                  <input
                    type="text"
                    name="prdSpec"
                    className="form-control"
                    value={item.prdDetail.prdSpec}
                    onChange={e => handleChange(e, 'prdDetail')}
                  />
                </td>
              </tr>

              <tr>
                <th className="bg-light">
                    (
                  <br />
                    )
                </th>
                <td colSpan="3">
                  <input
                    type="text"
                    name="prdExpire"
                    className="form-control"
                    value={item.prdDetail.prdExpire}
                    onChange={e => handleChange(e, 'prdDetail')}
                  />
                </td>
              </tr>

              <tr>
                <th className="bg-light"></th>
                <td colSpan="3">
                  <textarea
                    name="prdUsage"
                    className="form-control"
                    rows="3"
                    value={item.prdDetail.prdUsage}
                    onChange={e => handleChange(e, 'prdDetail')}
                  />
                </td>
              </tr>

              <tr>
                <th className="bg-light">
                   <br />
                  
                </th>
                <td>
                  <input
                    type="text"
                    name="prdManuf"
                    className="form-control"
                    value={item.prdDetail.prdManuf}
                    onChange={e => handleChange(e, 'prdDetail')}
                  />
                </td>
                <th className="bg-light"></th>
                <td>
                  <input
                    type="text"
                    name="prdOrigin"
                    className="form-control"
                    value={item.prdDetail.prdOrigin}
                    onChange={e => handleChange(e, 'prdDetail')}
                  />
                </td>
              </tr>

              <tr>
                <th className="bg-light"> </th>
                <td colSpan="3">
                  <input
                    type="text"
                    name="prdFda"
                    className="form-control"
                    value={item.prdDetail.prdFda}
                    onChange={e => handleChange(e, 'prdDetail')}
                  />
                </td>
              </tr>

              <tr>
                <th className="bg-light"> </th>
                <td colSpan="3">
                  <textarea
                    name="prdIngredients"
                    className="form-control"
                    rows="3"
                    value={item.prdDetail.prdIngredients}
                    onChange={e => handleChange(e, 'prdDetail')}
                  />
                </td>
              </tr>

              <tr>
                <th className="bg-light"></th>
                <td colSpan="3">
                  <textarea
                    name="prdCaution"
                    className="form-control"
                    rows="3"
                    value={item.prdDetail.prdCaution}
                    onChange={e => handleChange(e, 'prdDetail')}
                  />
                </td>
              </tr>

              <tr>
                <th className="bg-light"></th>
                <td colSpan="3">
                  <textarea
                    name="prdQuality"
                    className="form-control"
                    rows="3"
                    value={item.prdDetail.prdQuality}
                    onChange={e => handleChange(e, 'prdDetail')}
                  />
                </td>
              </tr>

              <tr>
                <th className="bg-light">
                   
                  <br />
                  
                </th>
                <td colSpan="3">
                  <input
                    type="text"
                    name="prdCs_phone"
                    className="form-control"
                    value={item.prdDetail.prdCs_phone}
                    onChange={e => handleChange(e, 'prdDetail')}
                  />
                </td>
              </tr>
            </tbody>
          </table>

          <div className="text-center mt-4">
            <button className="btn btn-secondary me-2" onClick={() => navigate(-1)}>
               
            </button>
            <button className="btn btn-success" onClick={handleSave}>
               
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductAdd;
