import { useEffect } from "react";

export default function LogoutPage() {
  useEffect(() => {
    try {
      localStorage.clear();
      sessionStorage.clear();
    } catch (e) {
      console.error("  :", e);
    }
    
    window.location.replace("/");
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return null;
}
