import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './RecentViewedRecommend.css';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const RecentViewedRecommend = () => {
  const navigate = useNavigate();

  const [products, setProducts] = useState([]);
  const [loaded, setLoaded] = useState(false);

  //  
  const user = JSON.parse(localStorage.getItem('user'));
  const userId = user?.userId;

  // =================================================
  //     API 
  // =================================================
  useEffect(() => {
    // case 1:        
    if (!userId) {
      setLoaded(true);
      setProducts([]);
      return;
    }

    axios
      .get(`${process.env.REACT_APP_API_URL}/api/products/list/recent`, {
        params: { userId },
      })
      .then(res => {
        const list = res.data.data || [];

        const converted = list.map(p => ({
          id: p.prdNo,
          name: p.prdName,
          brand: p.prdCompany,
          rating: p.avgRating || 0,
          reviewText: `${p.reviewCount || 0} `,
          img: `${process.env.PUBLIC_URL}/images/product/${p.prdImg}`,
        }));

        setProducts(converted);
        setLoaded(true);
      })
      .catch(() => {
        setLoaded(true);
        setProducts([]);
      });
  }, [userId]);

  // =================================================
  // case 1:      
  // =================================================
  if (!userId) {
    return null;
  }

  // =================================================
  // case 2:  O +    DB   
  // =================================================
  if (loaded && products.length === 0) {
    return null;
  }

  // =================================================
  // case 3:  O +    O  
  // =================================================
  return (
    <div className="container my-5">
      <div className="mb-3" style={{ textAlign: 'left' }}>
        <h4 className="fw-bold mb-1">    </h4>
        <p className="text-muted small mb-0">    </p>
      </div>

      <div className="row row-cols-1 row-cols-md-4 g-4">
        {products.slice(0, 4).map(p => (
          <div key={p.id} className="col">
            <div
              className="card h-100 border-0 shadow-sm product-card"
              style={{ cursor: 'pointer' }}
              onClick={() => navigate(`/products/${p.id}`)}
            >
              <img src={p.img} className="card-img-top" alt={p.name} />
              <div className="card-body">
                <h6 className="fw-semibold mb-1">{p.name}</h6>
                <p className="text-muted small mb-1">{p.brand}</p>
                <p className="text-warning small mb-0">
                   {p.rating}
                  <span className="text-muted"> | {p.reviewText}</span>
                </p>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default RecentViewedRecommend;
