import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import './RecentViewedProducts.css';
import api from 'app/api';

const RecentViewedProducts = () => {
  const navigate = useNavigate();
  const { recentProducts } = useSelector(state => state.user);
  const products = recentProducts || [];

  const handleProductClick = prdNo => {
    navigate(`/products/${prdNo}`);
  };

  const handleAddToCart = async (e, item) => {
    e.stopPropagation();
    try {
      await api.post('/api/cart/items', {
        productId: item.productId || item.prdNo,
        quantity: 1,
      });
      alert(' .');
    } catch (error) {
      console.error('  :', error);
      alert('  .');
    }
  };

  return (
    <div className="recent-viewed-container">
      <h2 className="page-title">  </h2>

      <div className="list-info">
        <div>
           <span className="count">{products.length}</span>
        </div>
        <div>    10, 3 .</div>
      </div>

      <div className="recent-table-header">
        <span className="col-product"></span>
        <span className="col-price"></span>
        <span className="col-date"> </span>
        <span className="col-manage"></span>
      </div>

      {products.length === 0 ? (
        <div className="empty-list">
          <div className="empty-icon">!</div>
          <p>   .</p>
        </div>
      ) : (
        <div className="recent-items">
          {products.map(item => (
            <div key={item.id} className="recent-item">
              <div className="col-product" onClick={() => handleProductClick(item.prdNo)} style={{ cursor: 'pointer' }}>
                <img src={item.image} alt={item.name} className="item-thumb" />
                <div className="item-text">
                  <span className="item-name">{item.name}</span>
                  <span className="item-brand">{item.brand}</span>
                </div>
              </div>
              <div className="col-price">
                {item.salePrice ? (
                  <div className="price-box">
                    <span className="original-price">{item.price.toLocaleString()}</span>
                    <span className="sale-price">{item.salePrice.toLocaleString()}</span>
                    <span className="discount-rate">{item.discount}%</span>
                  </div>
                ) : (
                  <span className="normal-price">{item.price.toLocaleString()}</span>
                )}
              </div>
              <div className="col-date">{item.viewedDate ? new Date(item.viewedDate).toLocaleDateString() : '-'}</div>
              <div className="col-manage">
                <button className="btn-cart" onClick={e => handleAddToCart(e, item)}>
                  
                </button>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default RecentViewedProducts;
