import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { AlertTriangle } from 'lucide-react';
import { withdrawUser } from '../../../features/user/userSlice';
import './MemberWithdrawal.css';

const MemberWithdrawal = ({ onCancel }) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { profile, currentUser } = useSelector(state => state.user);
  const [agreed, setAgreed] = useState(false);
  const [password, setPassword] = useState('');

  const handleWithdraw = async () => {
    if (!agreed) return;
    if (!password) {
      alert(' .');
      return;
    }

    // userNo  : profile.userNo   currentUser.userNo 
    let targetUserNo = profile?.userNo;

    // "me"     
    if (!targetUserNo || isNaN(Number(targetUserNo))) {
      if (currentUser?.userNo && !isNaN(Number(currentUser.userNo))) {
        targetUserNo = currentUser.userNo;
      }
    }

    if (!targetUserNo) {
      alert('    .  .');
      return;
    }

    if (window.confirm(' ?     .')) {
      try {
        // API  :    password  
        await dispatch(withdrawUser({ userNo: targetUserNo, password })).unwrap();
        alert('  .');
        navigate('/'); //  
      } catch (error) {
        console.error('Withdrawal failed:', error);
        //    (   )
        const errorMsg = error?.resultMsg || (typeof error === 'string' ? error : '   .');
        alert(errorMsg);
      }
    }
  };

  return (
    <div className="withdrawal-container">
      <h2 className="withdrawal-title"></h2>

      <div className="warning-box">
        <div className="warning-title">
          <AlertTriangle size={20} />
          <span>   </span>
        </div>
        <ul className="warning-list">
          <li>     ,       .</li>
          <li>  , ,       .</li>
          <li>     ,     .</li>
          <li>       .</li>
        </ul>
      </div>

      <div className="agreement-section">
        <label className="checkbox-label">
          <input type="checkbox" checked={agreed} onChange={e => setAgreed(e.target.checked)} />
          <span>   ,  .</span>
        </label>
      </div>

      <div className="password-section">
        <div className="input-group">
          <label> </label>
          <input
            type="password"
            placeholder="  "
            value={password}
            onChange={e => setPassword(e.target.value)}
          />
        </div>
      </div>

      <div className="button-group">
        <button className="btn-cancel" onClick={onCancel}>
          
        </button>
        <button className="btn-withdraw" disabled={!agreed || !password} onClick={handleWithdraw}>
          
        </button>
      </div>
    </div>
  );
};

export default MemberWithdrawal;
