import React, { useState, useEffect, useRef } from 'react';
import { Container, Row, Col, Spinner } from 'react-bootstrap';
import { useParams } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import api from 'lib/apiClient';
import { saveRecentProduct, fetchMyPageData } from 'features/user/userSlice';
import ProductImageGallery from 'components/user/details/ProductImageGallery';
import ProductInfo from 'components/user/details/ProductInfo';
import ProductDetailTabs from 'components/user/details/ProductDetailTabs';
import ReviewSummary from 'components/user/details/ReviewSummary';
import ProductIngredientAnalysis from 'components/user/details/ProductIngredientAnalysis';

const ProductDetailPage = () => {
  const { prdNo } = useParams(); // URL :prdNo   (: 101)
  const dispatch = useDispatch();
  const { currentUser } = useSelector(state => state.user);

  const [productData, setProductData] = useState(null); //    state
  const [loading, setLoading] = useState(true); //   

  //   ref
  const [activeTab, setActiveTab] = useState('desc'); //  
  const tabsRef = useRef(null); //      

  //  
  const handleMoveToReview = () => {
    setActiveTab('review'); //   
    tabsRef.current?.scrollIntoView({ behavior: 'smooth', block: 'start' });
  };

  //  
  const handleLikeToggle = async revNo => {
    //  
    if (!currentUser) return alert(' .');
    try {
      // API 
      await api.post(`/api/reviews/${revNo}/like`, null, {
        params: { userNo: currentUser?.userNo || 0 },
      });

      //   ,  ,    
      setProductData(prev => {
        // prev
        if (!prev) return null;

        //  ( )
        const updatedReviews = prev.reviewInfo.reviews.map(review => {
          if (review.revNo === revNo) {
            // 
            //  isliked liked      
            const currentStatus = review.liked || review.isLiked;

            const newStatus = !currentStatus; //  
            return {
              ...review, //...  
              liked: newStatus,
              isLiked: newStatus, //
              likeCount: newStatus ? review.likeCount + 1 : review.likeCount - 1,
            };
          }
          return review; //  
        });

        return {
          ...prev, // 
          reviewInfo: {
            ...prev.reviewInfo,
            reviews: updatedReviews,
          },
        };
      });
    } catch (error) {
      console.error(' :', error);
      alert(' .');
    }
  };

  // 
  const handleReviewUpdate = newReviewData => {
    setProductData(prev => {
      if (!prev) return null;
      return {
        ...prev,
        reviewInfo: newReviewData, //     
      };
    });
  };

  // 
  useEffect(() => {
    const fetchData = async () => {
      //
      try {
        setLoading(true); // 
        //    null  ?.  ,  undefined->null
        const analysisParams = {
          userId: currentUser?.userId || null,
          userSkin: currentUser?.userSkin || null, //   (: 'OILY', 'DRY')
        };

        //get   : /api/products/101
        const productRes = await api.get(`/api/products/${prdNo}`);
        const reviewRes = await api.get(`/api/products/${prdNo}/reviews`, {
          params: { userNo: currentUser?.userNo || 0 }, //    
        });
        //    params
        const ingredientRes = await api.get(`/api/products/${prdNo}/analysis`, { params: analysisParams });
        const productObj = productRes.data.data;

        //      
        if (currentUser?.userId && prdNo && productObj?.prdSubCate) {
          // UI      
          const productDetails = {
            id: productObj.prdNo,
            prdNo: productObj.prdNo,
            name: productObj.prdName,
            brand: productObj.prdCompany,
            price: productObj.prdPrice,
            salePrice: productObj.salePrice,
            discount: productObj.discountRate || productObj.discount,
            image: productObj.prdImg ? `${api.defaults.baseURL}/images/product/${productObj.prdImg}` : null,
            viewedDate: new Date().toISOString(),
          };

          dispatch(
            saveRecentProduct({
              userId: currentUser.userId,
              prdNo,
              prdSubCate: productObj.prdSubCate,
              productDetails, //  
            }),
          );
        }

        //   
        const combinedData = {
          productInfo: productObj, //  DTO , apiResponse   data.data
          reviewInfo: reviewRes.data.data || { summary: { totalCount: 0, averageRating: 0 }, reviews: [] }, //  
          ingredientInfo: ingredientRes.data.data,
          purchaseInfo: {},
        };

        setProductData(combinedData); // state ,  
      } catch (error) {
        //
        console.error('  :', error);
        alert('   .');
      } finally {
        setLoading(false); //   ( )
      }
    };

    if (prdNo) {
      //  
      fetchData();
    }
  }, [prdNo, currentUser, dispatch]);

  //   
  useEffect(() => {
    if (currentUser?.userId) {
      dispatch(fetchMyPageData(currentUser.userId));
    }
  }, [currentUser?.userId, dispatch]);

  //     
  if (loading) {
    return (
      <div className="d-flex justify-content-center align-items-center" style={{ height: '80vh' }}>
        <Spinner animation="border" variant="primary" /> {/*  */}
      </div>
    );
  }

  //   
  if (!productData) return <div className="text-center py-5">    .</div>;

  //  
  const { productInfo, reviewInfo, ingredientInfo, purchaseInfo } = productData;

  return (
    <Container className="my-5">
      <Row>
        <Col md={6}>
          {/*  6 , productInfo   */}
          <ProductImageGallery images={productInfo.images ? productInfo.images : { gallery: [] }} />
        </Col>

        <Col md={6}>
          {/* 6 ,    ,    */}
          <ProductInfo product={productInfo} reviewSummary={reviewInfo.summary} onMoveToReview={handleMoveToReview} />
        </Col>
      </Row>

      {/*,  */}
      <Row className="mt-5">
        <Col>
          <ReviewSummary reviewInfo={reviewInfo} onLikeToggle={handleLikeToggle} />
        </Col>
      </Row>
      <hr className="my-5" />

      {/*  */}
      <Row className="mt-5">
        <Col>
          <ProductIngredientAnalysis ingredientInfo={ingredientInfo} productInfo={productInfo} />
        </Col>
      </Row>

      <Row>
        <Col>
          <div ref={tabsRef} style={{ paddingTop: '20px' }}>
            {/*    */}
            <ProductDetailTabs
              activeTab={activeTab} //   
              onTabSelect={setActiveTab} // 
              productInfo={productInfo}
              purchaseInfo={purchaseInfo}
              reviewInfo={reviewInfo}
              ingredientInfo={ingredientInfo}
              onLikeToggle={handleLikeToggle}
              userInfo={currentUser}
              onReviewUpdate={handleReviewUpdate}
            />
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default ProductDetailPage;
