//    
export const getODStatusText = value => {
  if (value === 1) return '';
  if (value === 2) return '';
  if (value === 3) return '';
  if (value === 4) return '';
  if (value === 5) return '';
  if (value === 6) return '';
  if (value === 7) return '';
  if (value === 8) return '';
  return '';
};

//    
export const getDeliveryKeyText = (type, key) => {
  if (type === 2) return '';
  if (type === 1 && (key === null || key === undefined || key === '')) return '';
  return `: ${key ?? '-'}`;
};

//     
export const getTypeText = value => {
  if (value === 11) return '';
  if (value === 12) return '';
  if (value === 13) return '';
  if (value === 21) return '';
  if (value === 22) return '';
  if (value === 31) return '';
  return '';
};

//    
export const getStatusText = value => {
  if (value === 1) return '';
  if (value === 2) return '';
  if (value === 3) return '';
  if (value === 4) return ' ';
  if (value === 5) return '';
  if (value === 6) return ' ';
  return '';
};

// QA    
export const getQATypeText = value => {
  if (value === 1) return ' ';
  if (value === 2) return ' ';
  if (value === 3) return ' ';
  if (value === 7) return ' ';
  if (value === 8) return ' ';
  return '';
};

// QA    
export const getQAStatusText = value => {
  if (value === 1) return '';
  if (value === 2) return '';
  if (value === 3) return '';
  return '';
};

//    (null  )
export const formatDateParts = value => {
  if (!value) return { date: '-', time: '' };
  const parts = String(value).trim().split(/\s+/);
  return { date: parts[0] || '-', time: parts[1] || '' };
};
