import React, { useEffect, useState, useRef } from 'react';
import { useSearchParams, useNavigate } from 'react-router-dom';
import api from 'app/api';

function PaymentSuccessPage() {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [isConfirming, setIsConfirming] = useState(true);
  const [result, setResult] = useState(null);

  const hasCalledRef = useRef(false);

  const getParam = key => {
    return searchParams.get(key) || new URLSearchParams(window.location.search).get(key);
  };

  const paymentKey = getParam('paymentKey');
  const orderId = getParam('orderId');
  const amount = getParam('amount');

  useEffect(() => {
    if (hasCalledRef.current) return;
    async function confirmPayment() {
      try {
        hasCalledRef.current = true;
        //   
        await api.post('/api/payments/confirm', {
          paymentKey,
          orderId,
          amount: Number(amount),
        });

        //  
        setResult({
          type: 'SUCCESS',
          message: '    DB  .',
        });
      } catch (error) {
        console.error(error);
        setResult({
          type: 'ERROR',
          message: `[ ] ${error.message}`,
        });
      } finally {
        setIsConfirming(false);
      }
    }

    //   
    if (paymentKey && orderId && amount) {
      confirmPayment();
    } else {
      setIsConfirming(false);
      //     
      setResult({
        type: 'ERROR',
        message: ` : Key=${paymentKey}, Order=${orderId}, Amt=${amount}`,
      });
    }
  }, [paymentKey, orderId, amount]);

  if (isConfirming) {
    return (
      <div style={{ padding: '100px 20px', textAlign: 'center' }}>
        <h2>   ...</h2>
        <p>  .  .</p>
      </div>
    );
  }

  return (
    <div className="result-container" style={{ textAlign: 'center', padding: '50px' }}>
      {result.type === 'SUCCESS' ? (
        <>
          <div style={{ fontSize: '50px' }}></div>
          <h1 style={{ color: 'blue' }}> !</h1>
          <p>  .</p>
          <div
            style={{
              background: '#f8f9fa',
              padding: '20px',
              display: 'inline-block',
              borderRadius: '10px',
              marginTop: '20px',
            }}
          >
            <p>: {Number(amount).toLocaleString()}</p>
          </div>
          <br />
          <br />
          <div style={{ display: 'flex', gap: '10px', justifyContent: 'center' }}>
            <button
              onClick={() => navigate('/mypage')}
              style={{
                padding: '12px 30px',
                fontSize: '16px',
                backgroundColor: '#0d6efd',
                color: 'white',
                border: 'none',
                borderRadius: '5px',
                cursor: 'pointer',
              }}
            >
               
            </button>
            <button
              onClick={() => navigate('/')}
              style={{
                padding: '12px 30px',
                fontSize: '16px',
                backgroundColor: '#6c757d',
                color: 'white',
                border: 'none',
                borderRadius: '5px',
                cursor: 'pointer',
              }}
            >
              
            </button>
          </div>
        </>
      ) : (
        <>
          <h1 style={{ color: 'red' }}>  </h1>
          <p>{result.message}</p>
          <button onClick={() => navigate('/cart')}></button>
        </>
      )}
    </div>
  );
}

export default PaymentSuccessPage;
