import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { Header } from './Header';
import SideSticky from './SideSticky';
import api from '../../../lib/apiClient';
import { DEMO_RECENT_ITEMS } from './headerConstants';

/**
 *   . Header    ,
 * API       .
 */
const UserGlobalLayout = ({
  isLoggedIn,
  userRole,
  onLoginChange,
  onLoginClick,
  onSignupClick,
  onLogoutClick,
  onNavigate,
  onRoleChange,
  onRequireLogin,
  onCartClick,
}) => {
  const [recentItems, setRecentItems] = useState([]);
  const [cartCount, setCartCount] = useState(0);
  const { currentUser } = useSelector(state => state.user);
  const userId = currentUser?.userId;

  const handleScrollTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  //        API 
  useEffect(() => {
    if (!isLoggedIn || !userId) {
      setRecentItems([]);
      setCartCount(0);
      return;
    }

    // 1.    
    api
      .get(`/api/users/${userId}/recent-products`)
      .then(response => {
        const rawData = response.data;
        const list = Array.isArray(rawData) ? rawData : rawData?.data || [];

        if (Array.isArray(list)) {
          // SideSticky  (id, title, image) 
          const mappedItems = list.map(item => {
            return {
              id: item.prdNo,
              title: item.prdName,
              image: item.prdImg ? `${process.env.PUBLIC_URL}/images/product/${item.prdImg}` : null,
            };
          });
          //   (API    reverse )
          setRecentItems(mappedItems);
        } else {
          setRecentItems([]);
        }
      })
      .catch(error => {
        console.error('    :', error);
        // API      ( )
        setRecentItems(DEMO_RECENT_ITEMS);
      });

    // 2.   
    api
      .get(`/api/users/${userId}/cart/count`)
      .then(response => {
        //  : { resultCode: 200, resultMsg: "SUCCESS", data: { count: 3 } }
        const result = response.data;
        if (result && result.data && typeof result.data.count === 'number') {
          setCartCount(result.data.count);
        } else {
          setCartCount(0);
        }
      })
      .catch(error => {
        console.error('   :', error);
        setCartCount(0);
      });
  }, [isLoggedIn, userId]);

  return (
    <>
      <Header
        isLoggedIn={isLoggedIn}
        userId={userId}
        onLoginChange={onLoginChange}
        onLoginClick={onLoginClick}
        onSignupClick={onSignupClick}
        onLogoutClick={onLogoutClick}
        onNavigate={onNavigate}
        userRole={userRole}
        onRoleChange={onRoleChange}
        cartCount={cartCount}
      />
      <SideSticky
        isLoggedIn={isLoggedIn}
        recentItems={recentItems}
        onRequireLogin={onRequireLogin}
        onScrollTop={handleScrollTop}
        onNavigate={onNavigate}
      />
    </>
  );
};

export default UserGlobalLayout;
