import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { setUser } from '../features/user/userSlice';
import api from '../lib/apiClient';


const LoginPage = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const [formData, setFormData] = useState({
    userId: '',
    userPw: '',
  });

  const [errors, setErrors] = useState({});
  const [rememberMe, setRememberMe] = useState(false);

  const handleChange = e => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value,
    }));

    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: '',
      }));
    }
  };

  const handleSubmit = async e => {
    e.preventDefault();

    const newErrors = {};

    if (!formData.userId.trim()) {
      newErrors.userId = ' ';
    }

    if (!formData.userPw) {
      newErrors.userPw = ' ';
    }

    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);
      return;
    }

    try {
      const response = await api.post('/api/auth/login', {
        userId: formData.userId,
        userPw: formData.userPw,
      });

      if (response.data.token) {
        localStorage.setItem('token', response.data.token);

        const user = {
          userNo: response.data.userNo,
          userId: response.data.userId,
          userName: response.data.userName,
          userLevel: response.data.userLevel,
          userSkin: response.data.userSkin,
        };

        localStorage.setItem('user', JSON.stringify(user));
        dispatch(setUser(user));

        // userSkin 0   
        if (!user.userSkin || user.userSkin === 0) {
          alert(`${user.userName} !   .`);
          navigate('/skin-profile');
        } else {
          alert(`${user.userName} !`);
          navigate('/');
        }
      }
    } catch (error) {
      if (error.response?.status === 401) {
        alert('    .');
      } else if (error.response?.data?.message) {
        alert(error.response.data.message);
      } else {
        alert('   .');
      }
      console.error('Login error:', error);
    }
  };

  const handleKakaoLogin = () => {
    window.location.href = `${process.env.REACT_APP_API_URL}/auth/kakao/login`;
  };

  return (
    <div
      style={{
        minHeight: '100vh',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        background: 'linear-gradient(135deg, #E3F2FD 0%, #F3E5F5 100%)',
        padding: '40px 20px',
      }}
    >
      <div
        style={{
          background: 'white',
          borderRadius: '24px',
          padding: '50px 60px',
          width: '100%',
          maxWidth: '500px',
          boxShadow: '0 8px 32px rgba(0,0,0,0.08)',
        }}
      >
        {/*  */}
        <div style={{ textAlign: 'center', marginBottom: '40px' }}>
          <div
            style={{
              width: '80px',
              height: '80px',
              borderRadius: '50%',
              background: 'linear-gradient(135deg, #81D4FA 0%, #80CBC4 100%)',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              margin: '0 auto 20px',
              fontSize: '36px',
              fontWeight: '700',
              color: 'white',
            }}
          >
            R
          </div>
          <h1
            style={{
              color: '#42A5F5',
              fontSize: '32px',
              fontWeight: '700',
              margin: '0 0 10px 0',
            }}
          >
            Routy
          </h1>
          <p
            style={{
              color: '#666',
              fontSize: '14px',
              margin: 0,
            }}
          >
               Routy  
          </p>
        </div>

        <form onSubmit={handleSubmit}>
          {/*  */}
          <div style={{ marginBottom: '20px' }}>
            <label
              style={{
                display: 'block',
                marginBottom: '8px',
                fontSize: '14px',
                fontWeight: '600',
                color: '#333',
              }}
            >
              
            </label>
            <input
              type="text"
              name="userId"
              value={formData.userId}
              onChange={handleChange}
              placeholder=" "
              style={{
                width: '100%',
                padding: '16px 20px',
                border: 'none',
                borderRadius: '50px',
                fontSize: '15px',
                outline: 'none',
                backgroundColor: '#F3E5F5',
                transition: 'all 0.3s',
                boxSizing: 'border-box',
              }}
            />
            {errors.userId && (
              <p style={{ color: '#E57373', fontSize: '12px', marginTop: '5px', marginLeft: '15px' }}>
                {errors.userId}
              </p>
            )}
          </div>

          {/*  */}
          <div style={{ marginBottom: '20px' }}>
            <label
              style={{
                display: 'block',
                marginBottom: '8px',
                fontSize: '14px',
                fontWeight: '600',
                color: '#333',
              }}
            >
              
            </label>
            <input
              type="password"
              name="userPw"
              value={formData.userPw}
              onChange={handleChange}
              placeholder="8  "
              style={{
                width: '100%',
                padding: '16px 20px',
                border: 'none',
                borderRadius: '50px',
                fontSize: '15px',
                outline: 'none',
                backgroundColor: '#F3E5F5',
                transition: 'all 0.3s',
                boxSizing: 'border-box',
              }}
            />
            {errors.userPw && (
              <p style={{ color: '#E57373', fontSize: '12px', marginTop: '5px', marginLeft: '15px' }}>
                {errors.userPw}
              </p>
            )}
          </div>

          {/*    &   */}
          <div
            style={{
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              marginBottom: '30px',
              padding: '0 5px',
            }}
          >
            <label
              style={{
                display: 'flex',
                alignItems: 'center',
                cursor: 'pointer',
                fontSize: '14px',
                color: '#666',
              }}
            >
              <input
                type="checkbox"
                checked={rememberMe}
                onChange={e => setRememberMe(e.target.checked)}
                style={{
                  marginRight: '8px',
                  width: '18px',
                  height: '18px',
                  cursor: 'pointer',
                  accentColor: '#42A5F5',
                }}
              />
                
            </label>
            <button
              type="button"
              onClick={() => alert('   .')}
              style={{
                background: 'none',
                border: 'none',
                color: '#42A5F5',
                cursor: 'pointer',
                fontSize: '14px',
                fontWeight: '500',
              }}
            >
               
            </button>
          </div>

          {/*   */}
          <button
            type="submit"
            style={{
              width: '100%',
              padding: '18px',
              background: 'linear-gradient(135deg, #42A5F5 0%, #66BB6A 100%)',
              color: 'white',
              border: 'none',
              borderRadius: '50px',
              fontSize: '16px',
              fontWeight: '700',
              cursor: 'pointer',
              transition: 'all 0.3s',
              boxShadow: '0 4px 15px rgba(66, 165, 245, 0.3)',
            }}
          >
             
          </button>

          {/*    */}
          <button
            type="button"
            onClick={handleKakaoLogin}
            style={{
              width: '100%',
              padding: '18px',
              marginTop: '20px',
              background: '#FEE500',
              color: '#3C1E1E',
              border: 'none',
              borderRadius: '50px',
              fontSize: '16px',
              fontWeight: '700',
              cursor: 'pointer',
              boxShadow: '0 4px 15px rgba(0,0,0,0.1)',
            }}
          >
             
          </button>

          {/*   */}
          <div style={{ textAlign: 'center', marginTop: '20px' }}>
            <span style={{ color: '#666', fontSize: '14px' }}>  ? </span>
            <button
              type="button"
              onClick={() => navigate('/signup')}
              style={{
                background: 'none',
                border: 'none',
                color: '#42A5F5',
                cursor: 'pointer',
                fontSize: '14px',
                fontWeight: '600',
                textDecoration: 'none',
              }}
            >
              
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default LoginPage;
